import { createPopper } from '@popperjs/core';
import { type } from '../utils';

export function _newCreatePopper(
    reference: Element,
    popper: HTMLElement,
    placement: string | any,
    offset: number
): any {
    return createPopper(reference, popper, {
        placement: placement, // 
        modifiers: [
            {
                name: 'computeStyles',
                options: {
                    gpuAcceleration: false // top/left
                }
            },
            {
                name: 'computeStyles',
                options: {
                    adaptive: false // 
                }
            },
            {
                name: 'offset',
                options: {
                    offset: [offset] // 
                }
            }
        ]
    });
}

interface handleOptions {
    reference: Element;
    popper: Element | any;
    datasetVal: string;
    showCb: any;
    hideCb: any;
    delay: number;
    timer: any;
}

export function handleHoverShowAndHideEvents({
    reference,
    popper,
    datasetVal,
    showCb,
    hideCb,
    delay,
    timer
}: handleOptions): void {
    reference.addEventListener('mouseenter', () => {
        timer = setTimeout(() => {
            showEv();
        }, delay);
    });

    reference.addEventListener('mouseleave', hideEv);

    //  popper.dataset.tooltipShow 
    //  "true"  "false" 
    // 

    function showEv(): void {
        popper.dataset[datasetVal] = 'show';
        showCb && type.isFn(showCb);
    }

    function hideEv(): void {
        clearTimeout(timer);

        if (popper.dataset[datasetVal] === 'show') {
            popper.dataset[datasetVal] = 'hide';
            hideCb && type.isFn(hideCb);
        }

        reference.removeEventListener('mouseenter', showEv);
    }
}
