import { warn, CssTransition } from '../mixins';

interface Options {
    key?: string | number;
    prefixKey?: string;
    fadeOut?: boolean;
    clsLeave?: string;
    clsEnter?: string;
    destroy?: boolean;
    duration?: number;
    transitionTime?: number;
}

export function destroyElem(
    elem: any,
    {
        fadeOut = false,
        clsLeave,
        clsEnter,
        duration = 3,
        transitionTime = 250,
        destroy = true
    }: Options
): void {
    let timer = null;

    // 
    if (fadeOut) {
        isDismiss();
        CssTransition(elem, {
            inOrOut: 'out',
            enterCls: 'rab-fade-in',
            leaveCls: 'rab-fade-out',
            timeout: 250
        });
        return;
    }

    // 
    timer = setTimeout(() => {
        isDismiss();
        CssTransition(elem, {
            inOrOut: 'out',
            enterCls: clsEnter,
            leaveCls: clsLeave,
            timeout: transitionTime
        });
    }, duration * 1000);

    //  0 
    duration <= 0 ? clearTimeout(timer) : timer;

    // 
    function isDismiss() {
        setTimeout(() => {
            if (destroy) {
                elem.remove();
                elem = null; // 
            }
        }, transitionTime);
    }
}

export function destroyElemByKey(options: Options): void {
    const { prefixKey } = options;
    let { key } = options;
    // 
    typeof key !== 'string' ? (key = key?.toString()) : key;
    // key
    const target = document.querySelector(`[${prefixKey}-key="${key}"]`);
    target ? destroyElem(target, options) : warn(`The key value is invalid --> "${key}"`);
}
